import { Component, OnInit } from '@angular/core';
import { trigger, state, style, transition, animate, keyframes } from '@angular/animations';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { PostService } from '../post.service';
import { Router, ActivatedRoute, RoutesRecognized } from '@angular/router';
import { Subject } from 'rxjs';






@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css'],
  animations: [
    trigger('postAnim', [
      state('insert', style({
        opacity: 1,
        transform: 'translateX(0)'
      })),
      transition('void => *',[
        animate(1300, keyframes([
          style({
            opacity: 0,
            transform: 'translate(-100px)',
            offset: 0
          }),
          style({
            opacity: 0.5,
            transform: 'translate(-70px)',
            offset: 0
          }),
          style({
            opacity: 0.8,
            transform: 'translate(-70px)',
            offset: 0.3
          }),
          style({
            opacity: 1,
            transform: 'translate(0)',
            offset: 1
          })
        ]))
      ]),
      transition('* => void',[
        animate(900, style({
          opacity: 0,
          transform: 'translate(100px)'
        }))
      ])
    ])
  ]
})
export class PostsComponent implements OnInit {

  posts: { id: number, title: string, category: string, datePosted: string }[] = [];

  addForm: FormGroup;
  selectedPostId: number = -1;

  constructor(private postService: PostService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    this.addForm = new FormGroup({
      'title': new FormControl(null, Validators.required),
      'category': new FormControl(null, Validators.required),
      'date': new FormControl(null, Validators.required)
    });
    this.postService.getAllPosts()
    .subscribe(
      (posts) => {

        if(posts != null){

          this.posts = posts;
        }
      }
    );
  }

  deletePost(id: number){
    this.posts = this.posts.filter(posts => posts.id != id);
    this.postService.savePosts(this.posts)
    .subscribe(
      (successuful) => {

        let selectedId = this.postService.getSelectedPost();
        let selectedPost = this.posts.find(posts => posts.id == selectedId);
  
        if(selectedPost == null || selectedPost == undefined){

          this.router.navigate(['posts']);
        }
      }
    );

    
  }

  submit(){

    let idValue = 1;

    if(this.posts.length > 0){

      idValue = this.posts[this.posts.length - 1].id + 1
    }

    const newPost = {
      id: idValue, 
      title: this.addForm.value.title,
      category: this.addForm.value.category, 
      datePosted: this.addForm.value.date
    };
    this.posts.push(newPost);
    this.postService.savePosts(this.posts)
    .subscribe();

    this.addForm.reset();
  }
}

